.header_box {
    display: grid;
    grid-template-columns: 20%  10% 20% 10% 20%;
    width: 75%;
    justify-items: center;
}

.header_box h2 {
    font-size: 1.25rem;
    color: #f15a24;
}

.header_box h3 {
    padding: 0;
    margin: 0;
    font-size: 1rem;
    align-self: center;
}

.header_box span {
    border-right: 2px solid #777;
}

.time_box {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.time_box h3 {
    align-self: flex-start;
}

.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.expand_icon {
    height: 50px;
    padding: 0;
    margin-right: 25px;
    align-self: center;
    background-color: #ccc;
    border-radius: 15px;
}

.expand_icon:hover {
    cursor: pointer;
}

/*~~~~~~~~~~ MOBILE SUPPORT ~~~~~~~~~~*/
@media only screen and (max-width: 600px) {
    .header_box {
        width: 80%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 0;
    }

    .header_box span {
        display: none;
    }

    .expand_icon {
        height: 25px;
        margin: 0;
    }

    .header_box h2 {
        font-size: 1rem;
    }

    .header_box h3 {
        font-size: 0.75rem;
    }
}